<!--

Params:
  show: modal
  url: 
  title: modal
  params:  {xxId:}

self-done:
  boolean
  true
  false
self-model:
  v-model
-->
<template>
  <Modal v-model="modalShow"
         @on-cancel="cancel">
    <p slot="header" style="text-align:center">
      <Icon type="ios-information-circle" style="color: red"></Icon>
      <span>{{title?title:''}}</span>
    </p>
    <slot name="msg">
    </slot>
    <div slot="footer" style="text-align:center">
      <Button type="error" size="large" @click="ok"></Button>
    </div>
  </Modal>
</template>

<script>
  import ajaxUtil from '@/assets/util/ajaxUtil'
  export default {
    name: 'add',
    props: {
      show: Boolean,  // 
      url: String,    // url
      title: String,
      params: Object  // 
    },
    model: {
      prop: 'show',
      event: 'self-model'
    },
    data () {
      return {
        modal: this.show,
      }
    },
    computed: {
      modalShow:{
        get () {
          return this.show
        },
        set (value) {
          this.modal = value
        }
      }

    },
    methods: {
      ok () {
        let self = this
        ajaxUtil.ajax(self.url, self.params).done(function (response) {
          if (response.errorCode === "0000") {
            self.$Message.success('');
            self.$emit('self-done', true)
            self.$emit('self-model', false)
          } else {
            self.$Message.error('' + response.errorMsg);
          }
        }).fail(function (response) {
          self.$Message.error(':' + response.responseJSON.message);
        })
      },
      cancel () {
        this.$emit('self-done', false)
        this.$emit('self-model', false)
      }
    }
  }
</script>

<style scoped>

</style>



