import upperFirst from 'lodash/upperFirst'
import camelCase from 'lodash/camelCase'

function generateCompName(compPath) {
  return upperFirst(
    camelCase(
      //  `./` 
      compPath.replace(/^\.\/(.*)\.\w+$/, '$1')
    )
  )
}

export default {
  generateCompName
}



