<template>
  <div style="text-align: left">
    <h3 style="padding-left: 2em"></h3>
    <h3 style="padding-left: 2em"></h3>
    <Collapse v-model="unfold">
      <Panel name="1">
        token
        <div slot="content">
          <Button type="primary" @click="getToken">token</Button>
          <span style="margin-left: 3em">{{token}}</span>
        </div>
      </Panel>
      <Panel name="2">
        
        <div slot="content">
          <a href="https://www.aiqiyue.xyz/download/crawler-assistant.zip" download="crawler-assistant">crawler-assistant</a>
          <p></p>
        </div>
      </Panel>
      <Panel name="3">
        
        <div slot="content">
          <p></p>
          <p>google->-></p>
          <p></p>
          <p></p>
        </div>
      </Panel>
      <Panel name="4">
        token
        <div slot="content">
          <p></p>
          <p></p>
          <p>token</p>
          <p></p>
        </div>
      </Panel>
      <Panel name="5">
        
        <div slot="content">
          <p></p>
          <p></p>
          <p></p>
        </div>
      </Panel>
    </Collapse>
  </div>
</template>

<script>
  export default {
    name: "download",
    data () {
      return {
        unfold: "1",
        token: ''
      }
    },
    methods: {
      getToken () {
        this.token = this.$store.getters.userInfo.token
      }
    }
  }
</script>

<style scoped>

</style>



